.banners{
    width: 100%;
    height: auto;
    padding: 1rem;

    &__currentBanner{
        width: 100%;
        height: auto;
        margin: 0 auto;

        div{
            height: auto;
            border-radius: 8px;
            img{
                width: 97%;
                height: 250px;
                margin: 0 auto;
                object-fit: cover;
            }
        }
    }

    &__descriptionBanner{
        display: block;
        &__label{
            display: block;
            margin-top: 1rem;
        }

        &__input{
            width: 100%;
            padding: 1rem;
            border-radius: 8px;
            border: 1px solid #ccc;
        }
    }

    &__uploadBanner{
        width: 100%;
        &__label{
            display: block;
            margin-top: 1rem;
        }

        &__input{
            width: 100%;
            padding: 1rem;
            border: 1px solid #ccc;
            border-radius: 8px;
        }
    }

    &__submit{
        margin-top: 1rem;
        padding: .7rem;
        border-radius: 8px;
        color: #fff;
        font-size: 1rem;
        background-color: #125082;
        border: 1px solid #125082;
        cursor: pointer;
    }

    &__currentBannerMobile{
        display: flex;
        justify-content: space-between;
        grid-gap: 1rem;
        &__currentBanner{
            width: 50%;
            &__label{
                display: block;
            }
            div{
                border-radius: 8px;
                width: 100%;
                height: auto;
                img{
                    height: auto;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }

        &__description{
            display: block;
            width: 100%;

            &__box{
                width: 100%;
                margin-top: .5rem;
                &__label{
                    display: block;
                }
                &__input{
                    width: 100%;
                    border-radius: 8px;
                    padding: 1rem;
                    border: 1px solid #ccc;
                }
            }
        }
    }
}